:root {
  --size-like-button: 50px;
  --frames-button: 62;
}

input#input-heart {
  display: none;
}

.like {
  width: var(--size-like-button);
  height: var(--size-like-button);
  cursor: pointer;
  border-radius: 999px;
  overflow: visible;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

.hearth {
  background-image: url('https://svgshare.com/i/_7x.svg');
  background-size: calc(var(--size-like-button) * var(--frames-button)) var(--size-like-button);
  background-repeat: no-repeat;
  background-position-x: calc(var(--size-like-button) * (var(--frames-button) * -1 + 2));
  background-position-y: calc(var(--size-like-button) * 0.02);
  width: var(--size-like-button);
  height: var(--size-like-button);
}

input#input-heart:checked + .hearth {
  animation: like 1s steps(calc(var(--frames-button) - 3));  
  animation-fill-mode: forwards;
}

@keyframes like {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: calc(var(--size-like-button) * (var(--frames-button) * -1 + 3));
  }
}

@media (hover: hover) {
  .like:hover {
    background-color: #E1255E15;
    .hearth {
      background-position-x: calc(var(--size-like-button) * (var(--frames-button) * -1 + 1));
    }
  }
}

$bg: rgba(254,245,223, .3);


body {
  background-color: $bg;
}

.outside-container {
  display: flex;
  align-content: space-between;
  justify-content: center;
  flex-wrap: wrap;
}

.middle-category-container {
  margin: 20px;
}

.container {
  width: 300px;
  height: 400px;
  top:0; right: 0; left: 0; bottom: 0;
  margin: auto;
  background-color: black;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  
  -webkit-box-shadow: 0 0 5px #000;
        box-shadow: 0 0 5px #000;
  }

.overlay {
  width: 109%;
  height: 100%;
  margin-left: -4.3%;
  background: rgba(0, 0, 0, .5);
  color: $bg;
  opacity: 0;
  transition: all 0.5s;
  border-radius: 10px;
}

.category-writing-styling {
  color: rgba(254,245,223, 1);
  z-index: 10;
}

.items {
  padding-left: 20px;
  letter-spacing: 3px;
}

.head {
  font-size: 30px;
  line-height: 40px;
  text-align: left;
  padding-top: 10%;
  transform: translateY(40px);
  transition: all 0.7s;
  hr {
    display: block;
    width: 150px;
    border: none;
    border-bottom: solid 2px $bg;
    position: absolute;
    bottom: 0; 
    left:20px;
    top: 100px;
    transition: all .5s;
  }
}

.price {
  text-align: left;
  margin-top: 30%;
  font-size: 22px;
  line-height: 10px;
  font-weight: bold;  
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.7s;
}

.cart {
  display: flex;
  margin-top: 5%;
  text-align: left;
  font-size: 12px;
  opacity: 0;
  letter-spacing: 1px;
  font-family: 'Lato', sans-serif;
  transform: translateY(40px);
  transition: all 0.7s;
  i {
    font-size: 16px;
  }
  span {
    margin-left: 10px;
  }
}

.container:hover .overlay {
  opacity: 1;
  & .head {
    transform: translateY(0px);
  }
  
  & hr {
    width: 75px;
    transition-delay: 0.4s;
  }
  
  & .price {
    transform: translateY(0px);
    transition-delay: 0.3s;
    opacity: 1;
  }
  
  & .cart {
    transform: translateY(0px);
    transition-delay: 0.6s;
    opacity: 1;
  }
}

.middle-category-container a {
  text-decoration: none;
  color: rgba(254,245,223, 1);
}

* {
  .cursor {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  
    .cursor__pointer,
    .cursor__follow {
      position: absolute;
      width: 10px;
      height: 10px;
    }
  
    .cursor__pointer {
      border-radius: 100%;
      background-color: #fff;
    }
  
    .cursor__follow {
      padding: 32px;
      border: 1px solid #fff;
      transition: all 0.2s ease-out;
      border-radius: 100%;
    }
  }
}

.shop-top-description-2 {
  border-top: 1px dotted #d39700;
  padding-top: 1rem;
  width: 80%;
  height: 100px;
}

.positioning-arrow-to-middle {
  margin-left: .8rem;
}

.shop-top-margin-product {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
}

.links-category {
  padding-top: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  margin-top: 3rem;
}

h1.shop-heading-0 {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}

.container-border {
  border-radius: 10px;
}